<template>
  <main class="j_one">
    <div class="cm">
    <section>
      <h2>{{article.title}}</h2>
      <p v-html="article.body"></p>
    </section>
    </div>
  </main>
</template>

<script>
  import { DateTime } from 'luxon'
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'singlearticle',
    computed: {
      created () {
        if (this.article.createdOn) {
          return DateTime
            .fromISO(this.article.createdOn)
            .toLocaleString({ weekday: 'long', month: 'long', day: '2-digit', year: 'numeric' })
        }
        return ''
      },

      ...mapGetters({
        article: 'article'
      })
    },

    methods: {
      ...mapActions({
        load: 'article'
      })
    },

    beforeRouteEnter (to, from, next) {
      next(vm => {
        vm.load(vm.$route.params.id)
      })
    }
  }
</script>

<style scoped>
</style>
